import './App.css';
import React from 'react';
import axios from "axios";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
//import LogoESA from "./Media/Logo El Salvador.png"

//Vistas
import Inicio from "./Components/Inicio";
import Login from "./Components/Login";
import Usuario from "./Components/Usuario";
import Loading from './Components/Loading';

const base_URL ="http://localhost:8000";

class App extends React.Component {
  
  constructor(props){
    super(props);
    this.state={
      zonas :{
        idzona :"",
        nombrezona:"",
      },
      loading : true,
      timeout: 0,
    }
  }
 
  getZonas = async()=>{
    axios.get(base_URL+"/jsonzonas").then((res)=>{
      const z=res.data;
      this.setState({zonas :z})
    })
  }
 /* changeState (){
    this.setLoading({loading:true});
    setTimeout(()=>{
   this.setState({loading:false});}, 3000)
  }*/

  componentDidMount(){
    //this.changeState()
    this.getZonas()
  }
  
  render(){
    return (
        <Router>
          <div >
            {/*Seleccion de vistas*/}
            <Switch>
              {/*Peticion onload={getDatos}*/}
              <Route exact  path="/"><Loading/></Route>
              <Route exact  path="/inicio"><Inicio url={base_URL} nombrezona={this.state}/></Route>
              <Route exact path="/login"><Login /></Route>
              <Route exact path ="/usuario"><Usuario/></Route>
            </Switch>
          </div>
        </Router>
    );
  }
}

export default App;
